'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { useRouter } from 'next/navigation'

export default function HomePage() {
  const [nikAnak, setNikAnak] = useState('')
  const [noHp, setNoHp] = useState('')
  const [namaAnak, setNamaAnak] = useState('')
  const [namaOrangTua, setNamaOrangTua] = useState('')
  const [password, setPassword] = useState('')
  const [healthcareId, setHealthcareId] = useState('')
  const [healthcarePassword, setHealthcarePassword] = useState('')
  const [error, setError] = useState('')
  const router = useRouter()

  const handleParentLogin = () => {
    if (nikAnak && noHp && namaAnak && namaOrangTua && password) {
      // Format nomor HP dengan +62
      const formattedNoHp = noHp.startsWith('+62') ? noHp : 
                           noHp.startsWith('08') ? `+62${noHp.substring(1)}` : 
                           noHp.startsWith('8') ? `+62${noHp}` : noHp
      
      localStorage.setItem('parentData', JSON.stringify({
        nikAnak,
        noHp: formattedNoHp,
        namaAnak,
        namaOrangTua,
        password,
        loginTime: new Date().toISOString()
      }))
      
      // Buat key unik untuk data pasien
      localStorage.setItem(`parentData_${nikAnak}`, JSON.stringify({
        nikAnak,
        noHp: formattedNoHp,
        namaAnak,
        namaOrangTua,
        password,
        loginTime: new Date().toISOString()
      }))
      
      router.push('/parent')
    } else {
      setError('Mohon lengkapi semua data')
    }
  }

  const handleHealthcareLogin = () => {
    if (healthcareId && healthcarePassword) {
      localStorage.setItem('healthcareData', JSON.stringify({
        id: healthcareId,
        password: healthcarePassword,
        loginTime: new Date().toISOString()
      }))
      router.push('/healthcare')
    } else {
      setError('Mohon lengkapi ID dan password petugas')
    }
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="text-center mb-8">
        <div className="flex items-center justify-center gap-4 mb-4">
          <div className="w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
            <span className="text-2xl text-white font-bold">Z</span>
          </div>
          <div>
            <h1 className="text-4xl font-bold text-blue-900">ZINKKU ONLINE</h1>
            <p className="text-gray-600">Monitoring Zinc untuk Balita</p>
          </div>
        </div>
      </div>

      {/* Login Tabs */}
      <div className="max-w-md mx-auto">
        <Tabs defaultValue="parent" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="parent">Orang Tua</TabsTrigger>
            <TabsTrigger value="healthcare">Petugas</TabsTrigger>
          </TabsList>
          
          <TabsContent value="parent">
            <Card>
              <CardHeader>
                <CardTitle>Login Orang Tua</CardTitle>
                <CardDescription>
                  Masukkan data untuk memulai monitoring
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {error && (
                  <Alert variant="destructive">
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )}
                
                <div className="space-y-2">
                  <Label htmlFor="namaAnak">Nama Anak</Label>
                  <Input
                    id="namaAnak"
                    placeholder="Masukkan nama anak"
                    value={namaAnak}
                    onChange={(e) => setNamaAnak(e.target.value)}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="namaOrangTua">Nama Orang Tua</Label>
                  <Input
                    id="namaOrangTua"
                    placeholder="Masukkan nama orang tua"
                    value={namaOrangTua}
                    onChange={(e) => setNamaOrangTua(e.target.value)}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="nikAnak">NIK Anak</Label>
                  <Input
                    id="nikAnak"
                    placeholder="Masukkan NIK anak"
                    value={nikAnak}
                    onChange={(e) => setNikAnak(e.target.value)}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="noHp">No. HP (WhatsApp)</Label>
                  <Input
                    id="noHp"
                    placeholder="+6281234567890"
                    value={noHp}
                    onChange={(e) => setNoHp(e.target.value)}
                    type="tel"
                  />
                  <p className="text-xs text-gray-500">Contoh: +6281234567890 (dengan +62)</p>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="password">Password</Label>
                  <Input
                    id="password"
                    type="password"
                    placeholder="Masukkan password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </div>
                
                <Button 
                  onClick={handleParentLogin}
                  className="w-full bg-blue-600 hover:bg-blue-700"
                  disabled={!nikAnak || !noHp || !namaAnak || !namaOrangTua || !password}
                >
                  Masuk
                </Button>
              </CardContent>
            </Card>
          </TabsContent>
          
          <TabsContent value="healthcare">
            <Card>
              <CardHeader>
                <CardTitle>Login Petugas</CardTitle>
                <CardDescription>
                  Masukkan ID petugas untuk monitoring
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {error && (
                  <Alert variant="destructive">
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )}
                
                <div className="space-y-2">
                  <Label htmlFor="healthcareId">ID Petugas</Label>
                  <Input
                    id="healthcareId"
                    placeholder="Masukkan ID petugas"
                    value={healthcareId}
                    onChange={(e) => setHealthcareId(e.target.value)}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="healthcarePassword">Password</Label>
                  <Input
                    id="healthcarePassword"
                    type="password"
                    placeholder="Masukkan password"
                    value={healthcarePassword}
                    onChange={(e) => setHealthcarePassword(e.target.value)}
                  />
                </div>
                
                <Button 
                  onClick={handleHealthcareLogin}
                  className="w-full bg-green-600 hover:bg-green-700"
                  disabled={!healthcareId || !healthcarePassword}
                >
                  Masuk
                </Button>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>

      {/* WhatsApp Contact Section */}
      <div className="mt-8 max-w-4xl mx-auto">
        <Card className="bg-gradient-to-r from-green-50 to-blue-50 border-green-200">
          <CardHeader>
            <CardTitle className="text-center text-green-800">📱 WhatsApp Support</CardTitle>
            <CardDescription className="text-center">
              Konsultasi langsung dengan dokter via WhatsApp
            </CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <Button 
              onClick={() => window.open('https://wa.me/6285248980734', '_blank')}
              className="bg-green-600 hover:bg-green-700"
            >
              💬 Chat dr. Rizkina Yulianti
            </Button>
            <p className="text-sm text-gray-600 mt-2">
              +62 852-4898-0734
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Information Links */}
      <div className="mt-8 max-w-4xl mx-auto">
        <h2 className="text-2xl font-bold text-center mb-6 text-gray-800">Informasi Penting</h2>
        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-4">
          <Card className="cursor-pointer hover:shadow-lg transition-shadow" onClick={() => router.push('/info/zinc-benefits')}>
            <CardHeader className="text-center">
              <CardTitle className="text-lg text-blue-700">Manfaat Zinc</CardTitle>
            </CardHeader>
          </Card>
          <Card className="cursor-pointer hover:shadow-lg transition-shadow" onClick={() => router.push('/info/zinc-usage')}>
            <CardHeader className="text-center">
              <CardTitle className="text-lg text-green-700">Cara Menggunakan</CardTitle>
            </CardHeader>
          </Card>
          <Card className="cursor-pointer hover:shadow-lg transition-shadow" onClick={() => router.push('/info/zinc-dosage')}>
            <CardHeader className="text-center">
              <CardTitle className="text-lg text-purple-700">Dosis Zinc</CardTitle>
            </CardHeader>
          </Card>
          <Card className="cursor-pointer hover:shadow-lg transition-shadow" onClick={() => router.push('/info/oralit')}>
            <CardHeader className="text-center">
              <CardTitle className="text-lg text-orange-700">Cara Membuat Oralit</CardTitle>
            </CardHeader>
          </Card>
        </div>
      </div>
    </div>
  )
}
